.login-container {
  display: flex; /* Layout flexbox para centrar */
  justify-content: center; /* Centrado horizontal */
  align-items: center; /* Centrado vertical */
  min-height: 100vh; /* Altura mínima de viewport */
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%); /* Fondo gradiente azul-púrpura */
  padding: 20px; /* Espaciado interno */
}

.login-card {
  background: rgba(255, 255, 255, 0.95); /* Fondo semi-transparente blanco */
  border-radius: 20px; /* Bordes muy redondeados */
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1); /* Sombra profunda */
  padding: 40px; /* Espaciado interno amplio */
  width: 100%; /* Ancho completo */
  max-width: 400px; /* Ancho máximo */
  backdrop-filter: blur(10px); /* Efecto de desenfoque de fondo */
  border: 1px solid rgba(255, 255, 255, 0.2); /* Borde sutil */
}

.login-title {
  text-align: center; /* Alineación centrada */
  margin-bottom: 30px; /* Margen inferior */
  color: #00008B; /* Color azul oscuro para el título */
  font-size: 2rem; /* Tamaño de fuente grande */
  font-weight: 600; /* Peso semi-bold */
  /* Removido el gradiente para usar color sólido */
}

.login-form {
  display: flex; /* Layout flexbox */
  flex-direction: column; /* Dirección vertical */
}

.form-group {
  margin-bottom: 20px; /* Margen inferior entre grupos */
}

.form-group label {
  display: block; /* Display block para ocupar línea completa */
  margin-bottom: 8px; /* Margen inferior */
  color: #555; /* Color gris medio */
  font-weight: 500; /* Peso medio */
  font-size: 1rem; /* Tamaño estándar */
}

.form-group input {
  width: 100%; /* Ancho completo */
  padding: 12px 16px; /* Espaciado interno */
  border: 2px solid #e1e5e9; /* Borde gris claro */
  border-radius: 10px; /* Bordes redondeados */
  font-size: 1rem; /* Tamaño de fuente */
  transition: all 0.3s ease; /* Transición suave para todos los cambios */
  background: #f8f9fa; /* Fondo gris muy claro */
  color: #8B008B; /* Color de texto morado para modo claro */
  box-sizing: border-box; /* Box-sizing para incluir padding en ancho */
}

.form-group input:focus {
  outline: none; /* Sin outline por defecto */
  border-color: #667eea; /* Borde azul en foco */
  background: #f0f0f0; /* Fondo gris claro en foco */
  color: #8B008B; /* Color de texto morado en foco */
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1); /* Sombra azul sutil */
}

.form-group select {
  width: 100%; /* Ancho completo */
  padding: 12px 16px; /* Espaciado interno */
  border: 2px solid #e1e5e9; /* Borde gris claro */
  border-radius: 10px; /* Bordes redondeados */
  font-size: 1rem; /* Tamaño de fuente */
  transition: all 0.3s ease; /* Transición suave para todos los cambios */
  background: #f8f9fa; /* Fondo gris muy claro */
  color: #8B008B; /* Color de texto morado para modo claro */
  box-sizing: border-box; /* Box-sizing para incluir padding en ancho */
  cursor: pointer; /* Cursor de puntero */
}

.form-group select:focus {
  outline: none; /* Sin outline por defecto */
  border-color: #667eea; /* Borde azul en foco */
  background: #f0f0f0; /* Fondo gris claro en foco */
  color: #8B008B; /* Color de texto morado en foco */
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1); /* Sombra azul sutil */
}

.submit-btn {
  background: linear-gradient(135deg, #667eea, #764ba2); /* Gradiente azul-púrpura */
  color: white; /* Texto blanco */
  border: none; /* Sin borde */
  padding: 14px; /* Espaciado interno */
  border-radius: 10px; /* Bordes redondeados */
  font-size: 1.1rem; /* Tamaño de fuente ligeramente mayor */
  font-weight: 600; /* Peso semi-bold */
  cursor: pointer; /* Cursor de puntero */
  transition: all 0.3s ease; /* Transición suave */
  margin-top: 10px; /* Margen superior */
}

.submit-btn:hover {
  transform: translateY(-2px); /* Elevación en hover */
  box-shadow: 0 10px 20px rgba(102, 126, 234, 0.3); /* Sombra azul en hover */
}

.form-switch {
  margin-top: 20px; /* Margen superior */
  text-align: center; /* Alineación centrada */
}

.switch-btn {
  background: none; /* Sin fondo */
  border: none; /* Sin borde */
  color: #667eea; /* Color azul */
  cursor: pointer; /* Cursor de puntero */
  font-size: 0.9rem; /* Tamaño de fuente pequeño */
  margin: 5px; /* Margen pequeño */
  padding: 8px; /* Espaciado interno */
  border-radius: 5px; /* Bordes ligeramente redondeados */
  transition: background 0.3s ease; /* Transición suave para fondo */
}

.switch-btn:hover {
  background: rgba(102, 126, 234, 0.1); /* Fondo azul claro en hover */
}

.back-btn {
  background: #6c757d; /* Fondo gris */
  color: white; /* Texto blanco */
  border: none; /* Sin borde */
  padding: 10px 20px; /* Espaciado interno */
  border-radius: 10px; /* Bordes redondeados */
  font-size: 1rem; /* Tamaño estándar */
  cursor: pointer; /* Cursor de puntero */
  transition: all 0.3s ease; /* Transición suave */
  margin-top: 20px; /* Margen superior */
  width: 100%; /* Ancho completo */
}

.back-btn:hover {
  background: #5a6268; /* Fondo gris más oscuro en hover */
  transform: translateY(-1px); /* Elevación ligera */
}

/* Ajustes para modo oscuro */
.dark .login-card {
  background: rgba(33, 37, 41, 0.95); /* Fondo oscuro semi-transparente */
  color: #fff; /* Texto blanco */
}

.dark .login-title {
  color: #00008B; /* Título azul oscuro también en modo oscuro */
}

.dark .form-group label {
  color: #ccc; /* Etiquetas gris claro */
}

.dark .form-group input {
  background: #495057; /* Fondo gris medio */
  border-color: #6c757d; /* Borde gris */
  color: #fff; /* Texto blanco */
}

.dark .form-group input:focus {
  background: #343a40; /* Fondo gris más oscuro en foco */
  border-color: #667eea; /* Borde azul */
}

.dark .form-group select {
  background: #495057; /* Fondo gris medio para select en modo oscuro */
  border-color: #6c757d; /* Borde gris */
  color: #fff; /* Texto blanco */
}

.dark .form-group select:focus {
  background: #343a40; /* Fondo gris más oscuro en foco */
  border-color: #667eea; /* Borde azul */
}

.dark .switch-btn {
  color: #90caf9; /* Azul claro para botones de cambio */
}

.dark .switch-btn:hover {
  background: rgba(144, 202, 249, 0.1); /* Fondo azul claro en hover */
}